/* Variables */

:root {
  --color-blue: #2F89DF;
  --color-white: #FFFFFF;
  --color-gray-milk: rgba(245, 245, 245, 1);
  --color-dark-gray: #606060;
  --color-black-gray: #202020;
  --color-dark-gray-low: #555555;
  --color-yellow: #EEFF00;
  --color-invalid: #e61d1d;
  --font-roboto: "Roboto", "Helvetica Neue", Helvetica, Arial, sans-serif;
  --font-gotham: "GothamPro", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

/* Base */

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
}

/* Animation */

@-webkit-keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

/* Burger-menu */

@-webkit-keyframes rotate-burger-before {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(45deg);
  }
}

@keyframes rotate-burger-before {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(45deg);
  }
}

@-webkit-keyframes rotate-burger-after {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-45deg);
  }
}

@keyframes rotate-burger-after {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-45deg);
  }
}

/* Base Classes */

/* Containers */

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.container-full {
  padding-left: 20px;
  padding-right: 20px;
}

/* Hide */

.hide {
  display: none !important;
}

/* Position */

.left {
  text-align: left;
}

.center {
  text-align: center;
}

.right {
  text-align: right;
}

/* Background */

.bg--white {
  background-color: var(--color-white);
}

.bg--gray-milk {
  background-color: var(--color-gray-milk);
}

/* Links */

.link {
  text-decoration: none;
  transition: ease 0.3s;
}

.link:hover {
  color: var(--color-orange);
}

/* Block-padding */

.block-padding {
  padding: 48px 0;
}

/* Title */

.title {
  font-family: var(--font-gotham);
  font-style: normal;
  margin: 0;
}

.title--huge {
  font-size: calc(45px + 55 * ((100vw - 360px) / 1080));
}

@media screen and (min-width: 1440px) {

  .title--huge {
    font-size: 100px;
  }
}

@media screen and (max-width: 360px) {

  .title--huge {
    font-size: 45px;
  }
}

.title--big {
  font-size: calc(22px + 23 * ((100vw - 360px) / 1080));
}

@media screen and (min-width: 1440px) {

  .title--big {
    font-size: 45px;
  }
}

@media screen and (max-width: 360px) {

  .title--big {
    font-size: 22px;
  }
}

.title--av-big {
  font-size: calc(24px + 16 * ((100vw - 360px) / 1080));
}

@media screen and (min-width: 1440px) {

  .title--av-big {
    font-size: 40px;
  }
}

@media screen and (max-width: 360px) {

  .title--av-big {
    font-size: 24px;
  }
}

.title--large {
  font-size: calc(20px + 6 * ((100vw - 1024px) / 416));
}

@media screen and (min-width: 1440px) {

  .title--large {
    font-size: 26px;
  }
}

@media screen and (max-width: 1024px) {

  .title--large {
    font-size: 20px;
  }
}

.title--medium {
  font-size: calc(18px + 4 * ((100vw - 1024px) / 416));
}

@media screen and (min-width: 1440px) {

  .title--medium {
    font-size: 22px;
  }
}

@media screen and (max-width: 1024px) {

  .title--medium {
    font-size: 18px;
  }
}

.title--small {
  font-size: 16px;
}

.title--white {
  color: var(--color-white);
}

.title--dark-gray {
  color: var(--color-dark-gray);
}

.title--dark-gray-low {
  color: var(--color-dark-gray-low);
}

.title--w-light {
  font-weight: 300;
}

.title--w-normal {
