/* ANOTHER MODULES */

/* Blocks */

/* Page */

.page {
  position: relative;
  margin: 0;
  font-family: var(--font-roboto);
  font-size: 16px;
  line-height: 1.35;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  min-height: 100vh;
}

.page__container {
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  flex: 1 0 auto;
}

/* Button-to-top */

.button-to-top {
  position: fixed;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  background: var(--color-blue);
  box-shadow: 0 5px 20px rgba(34, 34, 34, 0.1);
  border: 1px solid rgba(34, 34, 34, 0.1);
  opacity: 0;
  visibility: hidden;
  border-radius: 50%;
  bottom: 25px;
  right: 25px;
  transition: opacity 0.5s ease-in;
  cursor: pointer;
  z-index: 1001;
}

.button-to-top.is-show {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.5s ease-in;
}

.button-to-top__svg {
  display: block;
  pointer-events: none;
}

/* Modal */

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
  background-color: transparent;
  padding-top: 100px;
  z-index: 9999;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.25);
  -webkit-backdrop-filter: blur(25px);
          backdrop-filter: blur(25px);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s ease-in-out;
  transition-delay: 0.3s;
  position: fixed;
}

.modal.is-open {
  visibility: visible;
  opacity: 1;
  position: fixed;
  transition-delay: 0s;
}

.modal__field--anim {
  opacity: 0;
  transform: translateX(-100px);
}

.modal__body {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 555px;
  width: 100%;
  padding: 30px 50px 50px 50px;
  overflow-x: hidden;
  border-radius: 25px;
}

.modal__close {
  display: inline-block;
  position: absolute;
  right: 15px;
  top: 15px;
  width: 13px;
  height: 13px;
  cursor: pointer;
}

.modal__close-svg {
  width: 13px;
  height: 13px;
  fill: var(--color-dark-gray);
}

