/* Footer */

.footer {
  padding: 60px 0;
}

.footer__inner {
  display: -ms-grid;
  display: grid;
  justify-items: center;
  -ms-grid-columns: minmax(200px, 288px) 25px minmax(150px, 1fr) 25px minmax(150px, 1fr);
  grid-template-columns: minmax(200px, 288px) minmax(150px, 1fr) minmax(150px, 1fr);
      grid-template-areas: "logo menu address";
  grid-gap: 25px;
}

.footer__logo {
  -ms-grid-row: 1;
  -ms-grid-column: 1;
  grid-area: logo;
}

.footer__logo-img {
  display: block;
  max-width: 200px;
}

