  -ms-grid-row: 1;
  -ms-grid-column: 3;
  grid-area: menu;
}

.footer__column--two {
  -ms-grid-row: 1;
  -ms-grid-column: 5;
  grid-area: address;
}

.footer__title-col {
  display: inline-block;
  padding-bottom: 10px;
